const rules = {
	min({ label, len = 3, info = null }) {
		return v => !!v ? v.length >= len || info || `[${label}] ${len}  .`: true;
	},
	require({label}) {
		return v => !!v || `[${label}]  .`;
	},
	alphaNum() {
		return v => !!v ? /^[a-zA-Z0-9_]+$/.test(v) || '  .' : true;
 	},
	pattern({label, pattern, info=null}) {
		return v => !!v ? pattern.test(v) || info || `[${label}]   ` : true;
	},
	matchValue(origin, info=null) {
		return v => v === origin || info || '  .';
	},
	id(options) {
		const defaultOptions = {
			label : "",
			len : 3,
			info : null,
			required: true
		};
		const opt = Object.assign(defaultOptions, options);
		const ruleArr= [];
		if(opt.required) {
			ruleArr.push(rules.require(opt));
		}
		ruleArr.push(rules.min(opt));
		ruleArr.push(rules.alphaNum());
		return ruleArr;
	},
	name(options) {
		const defaultOptions = {
			label : "",
			len : 2,
			info : null,
			required: true
		};
		const opt = Object.assign(defaultOptions, options);
		const ruleArr= [];
		if(opt.required) {
			ruleArr.push(rules.require(opt));
		}
		ruleArr.push(rules.min(opt));
		return ruleArr;
	},
	password(options) {
		const defaultOptions = {
			label : "",
			info : null,
			required: true,
			len : 6,
			pattern : /^.*(?=^.{6,}$)(?=.*\d)(?=.*[a-zA-Z]).*$/
		};
		const opt = Object.assign(defaultOptions, options);
		const ruleArr= [];
		if(opt.required) {
			ruleArr.push(rules.require(opt));
		}
		ruleArr.push(rules.min(opt));
		ruleArr.push(rules.pattern(opt));
		return ruleArr;
	},
	email(options) {
		const defaultOptions = {
			label : "",
			info : null,
			required: true,
			pattern : /.+@.+\..+/
		};
		const opt = Object.assign(defaultOptions, options);
		const ruleArr= [];
		if(opt.required) {
			ruleArr.push(rules.require(opt));
		}
		ruleArr.push(rules.pattern(opt));
		return ruleArr;
	},
	date(options) {
		const defaultOptions = {
			label : "",
			info : 'YYYY-MM-DD   ',
			required: true,
			pattern : /^\d{4}-\d{2}-\d{2}$/
		};
		const opt = Object.assign(defaultOptions, options);
		const ruleArr= [];
		if(opt.required) {
			ruleArr.push(rules.require(opt));
		}
		ruleArr.push(rules.pattern(opt));
		return ruleArr;
	},
	phone(options) {
		const defaultOptions = {
			label : "",
			info : null,
			required: true,
			pattern : /^(\d{2,3}-)?\d{3,4}-\d{4}$/
		};
		const opt = Object.assign(defaultOptions, options);
		const ruleArr= [];
		if(opt.required) {
			ruleArr.push(rules.require(opt));
		}
		ruleArr.push(rules.pattern(opt));
		return ruleArr;
	},
}

module.exports = rules;