<template>
  <div>
    <h1> </h1>
    <div>
      <v-btn @click="toastTest1"> Info</v-btn>
      <v-btn @click="toastTest2"> Success</v-btn>
      <v-btn @click="toastTest3"> Error</v-btn>
      <v-btn @click="toastTest4"> Warning</v-btn>
      <v-btn @click="toastTest5"> </v-btn>
    </div>
    <h1> </h1>
    <div>
      <v-btn @click="progressTest1">Start</v-btn>
      <v-btn @click="progressTest2">Finish</v-btn>
      <v-btn @click="progressTest3">Fail</v-btn>
    </div>
    <h1>Notify </h1>
    <div>
      <v-btn @click="notifyTest1">Alert</v-btn>
      <v-btn @click="notifyTest2">Confirm</v-btn>
      <v-btn @click="notifyTest3">Prompt</v-btn>
    </div>
    <h1>Axios </h1>
    <div>
      <v-btn @click="axiosTest1">Test</v-btn>
      <v-btn @click="axiosTest2">Error Test</v-btn>
    </div>
    <h1>Socket </h1>
    <div>
      <v-btn @click="joinRoom('testroom')"> </v-btn>
      <v-btn @click="leaveRoom('testroom')"> </v-btn>
      <v-btn @click="sendMsg(1)"></v-btn>
      <v-btn @click="sendMsg(2)"></v-btn>
      <v-btn @click="sendMsg(3)"></v-btn>
      <v-btn @click="sendMsg(4)"> </v-btn>
      <div>{{ $store.state.config.test1 }}</div>
    </div>
    <div>
      <v-text-field v-model="toId" label=""></v-text-field>
      <v-text-field v-model="userMsg" label=""></v-text-field>
      <v-btn @click="sendUser">  </v-btn>
    </div>
  </div>
</template>
<script>
import { mapActions } from "vuex";
export default {
  name: "Home",
  title() {
    return this.title;
  },
  data() {
    return {
      title: "My Home",
      toId: "",
      userMsg: "",
    };
  },
  socket() {
    return {
      "room:msg": (data) => {
        console.log("room:msg", data);
      },
			"room:chat" : (data) =>{
				console.log("room:chat", data);
				this.toId = data.fromId;
			}
    };
  },
  methods: {
    ...mapActions("socket", ["joinRoom", "leaveRoom"]),
    toastTest1() {
      this.$toast.info(" .");
    },
    toastTest2() {
      this.$toast.success("success");
    },
    toastTest3() {
      this.$toast.error("error.");
    },
    toastTest4() {
      this.$toast.warning("warning.");
    },
    toastTest5() {
      throw new Error(" ");
    },
    progressTest1() {
      this.$Progress.start();
    },
    progressTest2() {
      this.$Progress.finish();
    },
    progressTest3() {
      this.$Progress.fail();
    },
    async notifyTest1() {
      const res = await this.$ezNotify.alert("", "", {
        icon: "mdi-forum",
        iconColor: "yellow",
      });
      console.log(res);
    },
    async notifyTest2() {
      const res = await this.$ezNotify.confirm(" .");
      console.log(res);
    },
    async notifyTest3() {
      const res = await this.$ezNotify.prompt(
        " ?",
        " ?",
        { icon: "mdi-alert", text: " " }
      );
      console.log(res);
    },
    async axiosTest1() {
      const result = await this.$axios.get("/api/member/test");
      console.log(result);
    },
    async axiosTest2() {
      const result = await this.$axios.get("/api/errrrr/test");
      console.log(result);
    },
    sendMsg(target) {
      this.$socket.emit("room:send", { msg: target + "  ", target });
    },
    sendUser() {
      const { toId, userMsg } = this;
      const { member } = this.$store.state.user;
      if (toId && userMsg && member) {
        this.$socket.emit("room:chat", {
          toId,
          userMsg,
          fromId: member.mb_id,
        });
      }
    },
  },
};
</script>
