<template>
  <div>
		<!--   -->
		<v-card-text class="text-subtitle-1">
			<b>{{member.mb_name}}</b>
			({{LV_LABEL(member.mb_level)}})
		</v-card-text>
    <!--   -->
    <v-list v-if="isAdmin" dense>
      <v-subheader> </v-subheader>
      <v-list-item
        v-for="item in admMenus"
        :key="item.title"
        dense
        :to="item.to"
      >
        <v-list-item-icon>
          <v-icon>{{ item.icon }}</v-icon>
        </v-list-item-icon>
        <v-list-item-content>
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list>
    <!-- //   -->
    <v-card-actions>
      <v-btn color="primary" @click="$emit('open')" block></v-btn>
    </v-card-actions>
    <v-card-actions>
      <v-btn color="secondary" @click="logout" block></v-btn>
    </v-card-actions>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapMutations, mapState } from "vuex";
import { LV, LV_LABEL } from "../../../util/level";
export default {
  name: "MemberMenu",
  data() {
    return {
      admMenus: [
        { title: " ", icon: "mdi-cog", to: "/adm/config" },
        { title: " ", icon: "mdi-account-cog", to: "/adm/member" },
        { title: " ", icon: "mdi-menu", to: "/adm/menu" },
				{ title: " ", icon: "mdi-clipboard-text-multiple-outline", to: "/adm/board/list" },
      ],
    };
  },
  socket() {
    return {
      "member:admUpdate": async (newMember) => {
        let msg = "";
        // console.log(this.member.mb_level, newMember.mb_level);
        if (this.member.mb_level != newMember.mb_level) {
          if (newMember.mb_level < LV.MEMBER) {
            const mb_name = await this.signOut(); // 
            //  
            this.$toast.error(
              `${mb_name}      .`
            );
            //   
            if (this.$route.name != "Home") {
              this.$router.push("/");
            }
            return;
          } else {
            msg = `\n ${LV_LABEL(
              this.member.mb_level
            )}(${this.member.mb_level}) -> ${LV_LABEL(newMember.mb_level)}(${
              newMember.mb_level
            })`;
          }
        }
				msg = `${newMember.mb_name}     .` + msg;
				this.$toast.info(msg);
        this.SET_MEMBER(newMember);
      },
    };
  },
  computed: {
    ...mapState({
      member: (state) => state.user.member,
    }),
    ...mapGetters("user", ["isAdmin", "isSuper"]),
		LV_LABEL : () => LV_LABEL
  },
  methods: {
    ...mapMutations("user", ["SET_MEMBER"]),
    ...mapActions("user", ["signOut"]),
    async logout() {
      const mb_name = await this.signOut();
      if (mb_name) {
        this.$toast.info(`${mb_name}  .`);
        if (this.$route.name != "Home") {
          this.$router.push("/");
        }
      }
    },
  },
};
</script>

